.play-video {
    padding: 120px 0;
    margin-top: 170px;
    @include breakpoint(md) {
      margin-top: 0px; }
    @include breakpoint(xs) {
      padding: 70px 0; } }
.play-video-box {
    height: 500px;
    border-radius: 20px;
    position: relative;
    .rt-video-light {
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%); } }

