.single-rt-banner {
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    .rt-banner-content {
        @include breakpoint(xs) {
          padding-top: 150px; }
        h1 {
            font-size: 70px;
            font-weight: 700;
            line-height: 90px;
            margin: 0 0 0;
            @include breakpoint(md) {
                font-size: 55px;
                line-height: 70px;
                margin: 0 0 10px; }
            @include breakpoint(xs) {
                font-size: 40px;
                line-height: 52px; } }
        p {
            font-size: 18px;
            line-height: 28px;
            color: #191919;
            margin: 0 0 45px;
            @include breakpoint(xs) {
              font-size: 14px;
              line-height: 24px;
              margin: 0 0 20px; } }
        &.text-white {
            h1, p {
                color: #fff; } } } }

.rt-banner-height {
    height: 100vh;
    @include breakpoint(md) {
      height: 850px; } }


// slider dot and navigation
.rt-slider-active {
    .owl-dots {
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        bottom: 80px; }
    .owl-dot {
        @include hw(10px);
        background-color: $primary-color !important;
        display: inline-block;
        margin-right: 10px;
        border-radius: 50%;
        &:focus {
            outline: none; }
        &:last-child {
            margin-right: 0px; } }
    .owl-nav .owl-prev, .owl-nav .owl-next {
        position: absolute;
        left: 30px;
        top: 50%;
        transform: translateY(-50%);
        @include hw(45px);
        background-color: $primary-color !important;
        text-align: center;
        border-radius: 50%;
        color: #fff !important;
        font-size: 20px;
        &:focus {
            outline: none; } }
    .owl-nav .owl-next {
        left: auto;
        right: 30px; } }


// banner search
.rt-radio-group {
    padding-left: 18px;
    padding-right: 18px;
    margin-bottom: 15px; }
.rt-banner-searchbox {
    //height: 124px
    border-radius: 10px;
    background-color: rgb(255, 255, 255);
    box-shadow: 0px 10px 30px 0px rgba(17, 38, 117, 0.3);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 5px;
    padding-right: 5px;
    margin-bottom: 20px;
    padding-top: 15px;
    padding-bottom: 15px;
    overflow: hidden;
    @include breakpoint(xs) {
      height: auto;
      padding: 15px; } }

.rt-input-group {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    .col-rt-in-1, .col-rt-in-3 {
        padding-left: 5px;
        padding-right: 5px; }
    .col-rt-in-1 {
        @include breakpoint(xs) {
          @include flex-width(100%);
          text-align: center; } }
    .col-rt-in-3 {
        @include flex-width(23%);
        @include breakpoint(lg) {
          @include flex-width(22.77%); }
        @include breakpoint(xs) {
            @include flex-width(50%); } }

    .single-input {
        position: relative;
        @include breakpoint(xs) {
          margin-bottom: 10px;
          &:last-child {
              margin-bottom: 0px; } }

        .input-iconbadge {
            position: absolute;
            left: 20px;
            top: 50%;
            transform: translateY(-50%);
            i {
                color: rgba(79, 79, 79, 0.8); } } }
    .form-control {
        height: 53px;
        border-radius: 999px;
        padding-left: 15px;
        border: 1px solid rgba(79, 79, 79, 0.1);
        color: rgba(79, 79, 79, 0.8);
        box-shadow: 0px 5px 10px 0px rgba(55, 94, 246, 0.2);
        position: relative;
        font-size: 16px;
        &.has-icon {
            padding-left: 40px; }
        &::placeholder {
            color: rgba(79, 79, 79, 0.8);
            text-transform: capitalize; }
        @include breakpoint(xs) {
          height: 46px; } }
    &.mini {
        .form-control {
            padding-left: 20px;
            &::placeholder {
                font-size: 14px;
                color: #4f4f4f; } } }


    .single-input {
        button {
            height: 50px;
            width: 50px;
            border-radius: 10px;
            border: none;
            background-color: transpaent;
            background-image: linear-gradient( 8deg, rgb(153,50,245) 0%, rgb(73,51,241) 53%, rgb(73,80,248) 70%, rgb(73,108,254) 100%);
            color: #fff; } } }

.trip-search.rt-banner-searchbox {
    .single-input .input-iconbadge {
        left: 30px; } }

.rt-radio-group {
    .custom-control-label {
        font-family: $heading-font;
        font-size: 14px;
        color: #333333; }
    .rt-searchlink > a {
        font-family: $heading-font;
        font-size: 14px;
        color: #333333; }
    .custom-control-inline {
        margin-right: 22px; }
    .dropdown-menu {
        padding: 0;
        border-color: rgba(79, 79, 79, 0.1); }
    .dropdown-item {
        padding: 6px 18px; }
    .dropdown-toggle::after {
        border: none;
        content: '\ea99';
        font-family: "icofont";
        top: 0px;
        right: -14px;
        position: absolute;
        color: #333; } }

.rt-radio-group .custom-control-label::before {
    top: 8px;
    left: -18px;
    width: 12px;
    height: 12px;
    border: rgba(79, 79, 79, 0.8) solid 1px; }


.rt-radio-group  .custom-control-label::after {
    position: absolute;
    top: 8px;
    left: -18px;
    display: block;
    width: 12px;
    height: 12px; }



// tab button
.serachnavs {
    @include breakpoint(xs) {
      justify-content: center; }
    li {
        a {
            background-color: rgb(255, 255, 255);
            box-shadow: 0px 5px 10px 0px rgba(55, 94, 246, 0.2);
            height: 65px;
            width: 65px;
            text-align: center;
            border-radius: 5px;
            font-size: 14px;
            color: #4f4f4f;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            transition: all 0.24s ease-in-out;
            i {
                font-size: 18px; }
            &.active {
                background-image: linear-gradient( 8deg, rgb(153,50,245) 0%, rgb(73,51,241) 53%, rgb(73,80,248) 70%, rgb(73,108,254) 100%);
                color: #fff; } }


        margin-right: 10px;
        &:last-child {
            margin-right: 0px; } } }



#myTabContent {
    .rt-radio-group {
        animation: fadeInDown 400ms ease-in-out; }
    .rt-input-group {
        animation: fadeInUp 400ms ease-in-out; } }


// dark search
.rt-banner-searchbox {
    &.dark-search {
        background: #252c48;
        box-shadow: 0px 10px 30px 0px rgba(17, 38, 117, 0.3);
        .rt-radio-group .custom-control-label, .rt-searchlink > a {
            color: #fff; }
        .rt-input-group .col-rt-in-3 .form-control {
            background-color: rgba(#343d60, 1);
            border-color: rgba(42, 56, 76, 0.15);
            box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.2);
            color: #fff;
            &::placeholder {
                color: rgba(#6a79b9, 0.80); } }

        .rt-input-group .col-rt-in-3 .input-iconbadge i {
            color: rgba(#6a79b9, 0.80); } } }


.serachnavs {
    &.dark-search-nav {
        li {
            a {
                background-color: rgb(37, 44, 72);
                box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.2);
                color: #fff; } } } }

