.rt-breadcump {
  margin-bottom: 120px;
  @include breakpoint(xs) {
    margin-bottom: 70px; }
  background-color: #1d2a49;
  position: relative;
  color: #fff;
  a, p {
    color: #fff;
    font-size: 16px;
    position: relative; }





  h1,h2,h3,h4,h5,h6 {
    font-size: 70px;
    font-weight: 700;
    color: #fff;
    text-transform: capitalize;
    @include breakpoint(xs) {
      font-size: 32px; } }
  .rt-breadcump-height {
    height: 478px;
    &.with-logn-height {
      @include breakpoint(xs) {
        height: 650px; } } }
  .rt-page-bg {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    // &:after
    //   +after(absolute, 0, 0, 100%, 100%)
    //   background: linear-gradient( 8deg, rgb(153,50,245) 0%, rgb(73,51,241) 53%, rgb(73,80,248) 70%, rgb(73,108,254) 100%)
 }    //   +opacity(0.8)
  .breadcrumbs-content {
    padding-top: 247px;
    @include breakpoint(xs) {
      padding-top: 200px; } } }


