.counter-box-1 {
    background-color: #eef2f4;
    padding: 20px;
    border-radius: 5px;
    align-items: center;
    margin-bottom: 30px;
    img {
        margin-right: 40px; }
    h5 {
        font-family: $body-font;
        color: #000000;
        font-size: 16px;
        margin: 0 0 10px; }
    h6 {
        font-size: 40px;
        color: #496cfe;
        font-weight: 700; }
    &.dark-box {
        background-color: #252c48;
        h5 {
            color: #fff; } }
    &:hover {
        img {
            animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both; } } }

// ciounter box 2
.counter-box-2 {
    @include afterparent();
    margin-bottom: 30px;
    .counter-thumb {
        margin-bottom: 20px; }
    h5 {
        font-size: 40px;
        color: $primary-color;
        font-weight: 700;
        margin-bottom: 10px;
        line-height: 40px; }
    h6 {
        font-size: 14px;
        font-family: $body-font;
        color: #3d446f;
        margin: 0 0 0;
        font-weight: 400; }
    &:hover {
        .counter-thumb {
            img {
                animation: pulse 400ms  ease-in-out; } } } }
// icon box
@import "icon-box";
