

// top header import
@import "top-header";


.rt-site-header,.main-header {
    position: relative;
    .navbar {
        padding: 0px; }
    &.rt-fixed-top {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        z-index: 9; }
    &.rt-sticky {
        &.rt-sticky-active {
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            z-index: 99;
            box-shadow: 0 1px 10px 0 rgba(55,94,246,.1);
            background: #fff;
            .sticky-logo {
                display: block; }
            .brand-logo {
                display: none; } } }
    .sticky-logo {
        display: none; }
    .main-header {
        padding: 10px 0; } }




.rt-preloder {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    z-index: 9999;
    background-color: #fff;
    .preloder-box {
        height: 80px;
        width: 80px; } }
