.rt-site-footer {
    position: relative;
    margin-top: 120px;
    @include breakpoint(xs) {
      margin-top: 70px; }
    .rt-shape-emenetns-1 {
      position: absolute;
      left: -200px;
      top: 100px;
      width: 522px;
      height: 558px;
      @extend .rtbgprefix-contain;
      @include breakpoint(xl) {
        @include hw(400px); }
      @include breakpoint(md) {
        @include hw(300px);
        left: -100px; }
      @include breakpoint(xs) {
        display: none; } }
    .footer-top {
        padding-top: 120px;
        padding-bottom: 80px;
        @include breakpoint(xs) {
          padding-top: 70px;
          padding-bottom: 30px; }
        .rt-dot-divider {
          border-bottom: 1px dotted rgba(204, 204, 205, 0.20);
          padding-bottom: 50px;
          margin-bottom: 120px;
          @include breakpoint(xs) {
            margin-bottom: 70px; } }
        .footer-subscripbe-box {
          .btn {
            font-size: 20px;
            @extend .rt-gradient;
            border-radius: 999px;
            padding: 0 40px;
            right: 3px;
            top: 3px;
            height: 51px;
            position: absolute;
            font-size: 14px;
            font-family: $heading-font;
            font-weight: 600;
            text-transform: uppercase;
            &:after {
              border-radius: 999px; } }
          .form-control {
            background: transparent;
            position: relative;

            border-color:  rgba(255, 255, 255, 0.4);
            height: 57px;
            padding-left: 28px;
            border-radius: 999px;
            &:focus {
              border-color: $primary-color; }
            &::placeholder {
              color: rgba(#d4d4d8, 0.70);
              font-weight: 300;
              font-size: 14px; } } } }
    .footer-bottom {
        padding: 15px 0;
        background-color: #22212f;
        .copy-text {
          color: #fff;
          font-size: 14px;
          a {
            color: $primary-color; } } } }



.rt-single-widget {
  margin-bottom: 40px;
  a {
    color: $primary-color; }
  .rt-footer-title {
    margin-bottom: 25px;
    font-size: 24px;
    font-weight: 600;
    color: #fff; }

  ul {
    margin: 0;
    padding: 0;
    list-style: none;
    li {
      display: block;
      margin-bottom: 14px;
      position: relative;
      &:after {
        position: absolute;
        left: -10px;
        top: 50%;
        transform: translateY(-50%);
        color: #fff;
        content: '\ea7c';
        transition: all 0.24s ease-in-out;
        font-family: 'icofont'; }
      &:last-child {
        margin-bottom: 0px; }
      a {
        display: block;
        color: #fff;
        transition: all 0.24s ease-in-out;
        font-size: 14px;
        padding-left: 10px;
        &:hover {
          color: #ccc;
          padding-left: 12px; } }
      &:hover {
        &:after {

          color: #ccc; } } } } }


//footer social
.rt-footer-social {
    ul {
        margin: 0;
        padding: 0;
        list-style: none;
        li {
            display: inline-block;
            margin-right: 8px;
            &:last-child {
                margin-right: 0px; }
            a {
                display: block;
                font-size: 18px;
                color: $secondary-color;
                @include rt-trs(0.24sç);
                img {
                  height: 25px;
                  @include rt-trs(0.24s); }
                &:hover {
                  color: $primary-color;
                  img {
                    transform: translateY(-5px) skewX(2deg); } } } } } }
