// fligt deals css

.flight-box {
    @include afterparent();
    height: 229px;
    box-shadow: 0px 10px 20px 0px rgba(51, 79, 181, 0.5);
    @extend .rtbgprefix-cover;
    margin-bottom: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 30px;
    border-radius: 10px;
    @include rt-trs(0.24s);
    .rt-inner-overlay {

        border-radius: 10px;
        opacity: 0.7;
        &.color_1 {
            background-image: linear-gradient( -8deg, rgb(153,50,245) 0%, rgb(73,51,241) 53%, rgb(73,80,248) 70%, rgb(73,108,254) 100%); }
        &.color_2 {
            background-image: linear-gradient( 0deg, rgb(254,92,118) 0%, rgb(255,170,87) 100%); }
        &.color_3 {
            background-image: linear-gradient( -8deg, rgb(218,48,155) 0%, rgb(180,96,205) 37%, rgb(141,144,255) 100%); }
        &.color_4 {
            background-image: linear-gradient( -8deg, rgb(3,64,131) 0%, rgb(46,105,255) 0%, rgb(67,122,253) 37%, rgb(88,138,251) 100%); }
        &.color_5 {
            background-image: linear-gradient( 0deg, rgb(255,170,87) 0%, rgb(254,92,118) 100%); }
        &.color_6 {
            background-image: linear-gradient( 0deg, rgb(1,223,184) 0%, rgb(73,229,65) 100%); } }
    .trip-to, .trip-form {
        span {
            display: block;
            color: #fff;
            font-family: $heading-font;
            text-transform: uppercase;
            &:nth-child(1) {
                font-size: 14px; }
            &:nth-child(2) {
                font-size: 28px;
                font-weight: 600; } } }
    .trip-to {
        margin-bottom: 41px; }
    &.dark-box {
        box-shadow: 0px 10px 20px 0px rgba(0, 0, 0, 0.5); }
    &:hover {
        transform: translateY(-5px);
        box-shadow: 0px 10px 30px 0px rgba(51, 79, 181, 0.5);
        &.dark-box {
            box-shadow: 0px 10px 30px 0px rgba(0, 0, 0, 0.5); } } }




.flight-dela-area {
  @include afterparent();
  .rt-shape-emenetns-1, .rt-shape-emenetns-2 {
    position: absolute; }
  .rt-shape-emenetns-1 {
      left: 0;
      top: 150px;
      width: 827px;
      height: 836px;
      @extend .rtbgprefix-contain;
      @include breakpoint(xl) {
        width: 700px; }
      @include breakpoint(lg) {
        width: 550px; }
      @include breakpoint(md) {
        width: 400px;
        height: 600px; }
      @include breakpoint(xs) {
        display: none; } }

  .rt-shape-emenetns-2 {
      right: -40px;
      top: 200px;
      width: 750px;
      height: 750px;
      @extend .rtbgprefix-full;
      @include breakpoint(xl) {
        height: 500px;
        width: 500px; }
      @include breakpoint(xs) {
        display: none; } } }

// flight sidebar

.rt-widget {
    border: 1px solid rgba(42, 56, 76, 0.15);
    border-radius: 5px;
    box-shadow: 0px 10px 30px 0px rgba(55, 94, 246, 0.2);
    background-color: #fff;
    padding: 30px;
    margin-bottom: 30px;
    &:last-child {
        margin-bottom: 0px; }
    @include afterparent();
    .rt-widget-title {
        font-size: 18px;
        font-weight: 600;
        margin: 0 0 24px;
        padding: 0 0 20px;
        border-bottom: 1px solid  rgba(32, 32, 32, 0.078); }
    &.widget_range-slider {
        .rt-widget-title {
            margin: 0 0 40px; } }
    ul {
        margin: 0;
        padding: 0;
        list-style: none;
        li {
            font-size: 14px;
            display: block;
            padding-bottom: 5px;
            &:last-child {
                padding-bottom: 0px; }
            label {
                padding-left: 5px; } } }
    .show-more-links {
        padding-top: 10px;
        a {
            font-size: 14px;
            color: #496cfe;
            &:hover {
                 color: darken(#496cfe,  10%);
                 text-decoration: underline; } } }
    &.final-booking {
        li {
            font-size: 16px;
            color: #000; }
        li.sub-total {
            border-top: 1px solid rgba(0, 0, 0, 0.502);
            padding-top: 15px;
            padding-bottom: 30px;
            margin-top: 7px;
            span {
                &:nth-child(1) {
                    font-size: 18px;
                    font-weight: 600; }
                &:nth-child(2) {
                    font-size: 28px;
                    font-weight: 600;
                    color: #496cfe; } } } } }



.flight-list-box {
    border: 1px solid rgba(13, 21, 75, 0.15);
    box-shadow: 0px 3px 7px 0px rgba(55, 94, 246, 0.2);
    @include rt-trs(0.24s);
    padding: 18px;
    background-color: #fff;
    @include afterparent();
    .flight-logo {
        border: 1px solid  rgba(73, 108, 254, 0.2);
        width: 79px;
        height: 86px;
        line-height: 86px;
        text-align: center;
        @include breakpoint(md) {
          margin-bottom: 15px; } }
    .pricing {
        @include breakpoint(md) {
          margin-bottom: 15px; }
        h5 {
            font-size: 16px;
            font-weight: 500; }
        p {
            font-size: 12px;
            color: #666363;
            margin: 0 0 0; } }
    .flight-time {
        @include breakpoint(md) {
          margin-bottom: 15px; }
        .left, .right {
            span {
                &:nth-child(1) {
                    font-size: 20px;
                    font-weight: 600;
                    color: #000; }
                &:nth-child(2) {
                    font-size: 12px;
                    color: #666363;
                    text-transform: uppercase; } } }
        .middle {
            padding-left: 12px;
            padding-right: 12px; } }

    .flight-detils {
        @include breakpoint(md) {
          margin-bottom: 15px; }
        span {
            &:nth-child(1) {
                font-size: 12px;
                color: #666363; }
            &:nth-child(2) {
                font-size: 13px;
                color: #496cfe; } } }
    .trip {
        @include breakpoint(md) {
          margin-bottom: 15px; }
        span {
            &:nth-child(1) {
                font-size: 24px;
                color: #496cfe;
                font-family: $heading-font;
                font-weight: 600; }
            &:nth-child(2) {
                font-size: 12px;
                color: #333333; } } }
    .book-now {
        .rt-btn {
            padding: 10px 30px;
            font-size: 12px; } }

    .top-content {
        padding-bottom: 15px; }
    .bottom-content {
        // height: 0
        // overflow: hidden
        // transition: all 2s ease-in-out
        border-top: 1px solid rgba(73, 108, 254, 0.6);
        padding-top: 15px;
        p {
            span {
                font-size: 12px;
                color: #666363;
                &:nth-child(1) {
                    margin-right: 15px; } } } }
    &.pt-30 {
        padding: 30px; }


    &:hover {
        border-color: $primary-color;
        .bottom-content {
 } } }            // height: auto




.flight-timeline {
    margin: 0;
    padding: 0;
    list-style: none;
    width: 70%;
    @include afterparent;
    &:after {
        clear: both;
        display: block;
        content: ""; }
    &:before {
            position: absolute;
            left: 20%;
            top: 50%;
            transform: translateY(-50%);
            height: 36px;
            content: '';
            background-color: rgb(156, 169, 222);
            width: 2px; }
    li {
        font-size: 14px;
        color: #000000;
        &:first-child {
            &:after {
                position: absolute;
                right: -3px;
                top: 7px;
                width: 5px;
                height: 5px;
                border-radius: 50%;
                background-color: rgb(156, 169, 222);
                content: ""; } }
        &:last-child {
            &:after {
                position: absolute;
                left: -1px;
                bottom: 7px;
                width: 5px;
                height: 5px;
                border-radius: 50%;
                background-color: rgb(156, 169, 222);
                content: ""; } }
        @include afterparent();

        &:nth-child(2n) {
            float: right;
            width: 80%;
            padding-left: 30px; }
        &:nth-child(2n + 1) {
            float: left;
            width: 20%; } } }


.flt-dtls-box {
    .upper-top-content {
        background-image: linear-gradient( 90deg, rgb(73,108,254) 0%, rgb(73,80,248) 29%, rgb(73,51,241) 55%, rgb(113,51,243) 83%, rgb(153,50,245) 100%);
        padding: {
            left: 30px;
            right: 30px;
            top: 25px;
            bottom: 25px; }
        .left {
            span {
                margin-right: 15px;
                color: #ffffff;
