.author-box {

    .author-title {
          font-size: 26px;

          font-weight: 700;
          text-transform: capitalize;
          margin:  60px 0 60px; }
    .bio-thumb {
        img {
            border: 3px solid #fff;
            box-shadow: 0px 5px 10px 0px rgba(46, 25, 167, 0.24); } }
    .auhor-bio-data {
        border-top: 1px solid;
        border-bottom: 1px solid;
        border-color: rgba(15, 54, 97, 0.5);
        padding: 30px 0;
        font-size: 15px;

        h5 {
            font-size: 18px;
            color: #000;
            font-weight: 700;
            margin: 0 0 20px; } } }





