.rt-border-brands {
    display: flex;
    flex-wrap: wrap;
    margin: 0;
    padding: 0;
    list-style: none;
    .single-border-brands {
        @include flex-width(25%);
        justify-content: center;
        text-align: center;
        border-right: 1px solid rgba(#034083, 0.20);
        border-bottom: 1px solid rgba(#034083, 0.20);
        padding: {
            left: 30px;
            right: 30px;
            top: 40px;
            bottom: 40px; }
        &:nth-child(5),
        &:nth-child(6),
        &:nth-child(7),
        &:nth-child(8) {
            border-bottom: none; }
        &:nth-child(4),
        &:nth-child(8) {
            border-right: none; }
        a {
            img {
                @include rt-trs(0.34s);
                &:hover {
                    filter: blur(1px) grayscale(100%);
                    transform: translateY(-5px) skewX(10deg); } } }
        @include breakpoint(md) {
            @include flex-width(33.33%);
            &:nth-child(4),
            &:nth-child(8) {
                border-right: 1px solid rgba(#034083, 0.20); }
            &:nth-child(5),
            &:nth-child(6) {
                border-bottom: 1px solid rgba(#034083, 0.20); }
            &:nth-child(3),
            &:nth-child(6) {
                border-right: none; } }
        @include breakpoint(xs) {
            @include flex-width(50%);
            &:nth-child(3) {
                border-right: 1px solid rgba(#034083, 0.20); }

            &:nth-child(2),
            &:nth-child(4),
            &:nth-child(6),
            &:nth-child(8) {
                border-right: none; } } }


    &.dark-box {
        .single-border-brands {
            border-color: rgba(#fff, 0.20);
            border-color:  rgba(#fff, 0.20); } } }



