

$theme-colors: () !default;
$theme-colors: map-merge((
        "Primary__color":    $primary-color,
        "Secondary__color":  $secondary-color,

), $theme-colors);



:root {


  @each $color, $value in $theme-colors {
    --#{$color}: #{$value};
  }




}


// color
.primary-color, a.primary-color{
  color: $primary-color;
  
}
.secondary-color, a.secondary-color{
  color: $secondary-color;
}

.primary-color-bg{
  background-color: $primary-color;
}
.secondary-color-bg{
  background-color: $secondary-color;
}

// section background color
.has-gray-bg{
  background-color: gray;
}

.rt-dark{
  background-color: #151932;
}

.rt-light-bg{
  background-color: #fff;
}

.rt-light-gray{
  background-color: #f5f9ff;
}

.rt-dim-light{
  background-color: #f4eafe;
}

// text color 
.text-333, a.text-333{
  color: #333;
}
.text-555, a.text-555{
  color: #555;
}

.text-777, a.text-777{
  color: #777777;
}

.text-878, a.text-777{
  color: #878484;
}

.text-2f7, a.text-777{
  color: #2f7000;
}
.text-656{
  color: #656f7f;
}
.text-444{
  color: #444;
}
.heading-color-1{
  color: #000;
}
.text-424{
  color: #42435d;
  p, a{
    color: #42435d;
  }
}

// text gradinet color 

.text-gradinet-primary{
  background: $gradient-primary;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.bg-gradient-primary{
   background: $gradient-primary;
}


