html {
  height: 100%;
  font-size: 62.5%;
  @include breakpoint(lg) {
    font-size: 60%; }
  @include breakpoint(md) {
    font-size: 55%; }
  @include breakpoint(xs) {
    font-size: 50%; }
  @media only screen and (max-width: 479px) {
    font-size: 45%; }
  @include breakpoint(xss) {
    font-size: 40%; } }



// heading

h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {

  font-family: $heading-font;
  color: $headnig-font-color; }
h1,
.h1 {
  font-size: 42px;
  @include breakpoint(md) {
    font-size: 40px; }
  @include breakpoint(xs) {
    font-size: 38px; } }

h2,
.h2 {
  font-size: 38px;
  @include breakpoint(md) {
    font-size: 36px; }
  @include breakpoint(xs) {
    font-size: 34px; } }

h3,
.h3 {
  font-size: 30px;
  @include breakpoint(md) {
    font-size: 28px; }
  @include breakpoint(xs) {
    font-size: 25px; } }

h4,
.h4 {
  font-size: 25px;
  @include breakpoint(md) {
    font-size: 23px; }
  @include breakpoint(xs) {
    font-size: 20px; } }

h5,
.h5 {
  font-size: 18px;
  text-transform: capitalize; }



h6,
.h6 {
  font-size: 16px; }

p {
  font-size: $default-font-size;
  font-family: $body-font;
  line-height: 26px; }


.title-font {
  font-family: $heading-font !important; }
.text-font {
  font-family: $body-font !important; }

.rt-section-title {
  text-transform: capitalize;
  font-size: 60px;
  margin: 0 0 20px;
  font-weight: 700;
  @include breakpoint(md) {
    font-size: 45px; }
  @include breakpoint(xs) {
    font-size: 32px; }
  span {
    color: $primary-color;
    display: block;
    font-size: 18px;
    text-transform: uppercase;
    font-family: $body-font;
    margin: 0 0 0;
    @include breakpoint(xs) {
      font-size: 14px; } } }

.text-white {
  .rt-section-title {
    color: #ffffff;
    span {
      color: #fff; } }
  h1,h2,h3,h4,h5,h6 {
    color: #fff; } }
.text-white2 {
  .rt-section-title {
    color: #ffffff;
    span {
      color: #496cfe; } }
  h1,h2,h3,h4,h5,h6 {
    color: #fff; }
  p {
    color: #fff; } }



.rt-strong {
  font-weight: 700; }
.rt-medium {
  font-weight: 500; }
.rt-semiblod {
  font-weight: 600; }
.underline {
  text-decoration: underline; }


.badge-hilighit {
  position: relative;
  display: inline-block;
  padding: 10px 40px;
  &:before {
    border-bottom: 18px solid transparent;
    border-left: 7px solid #fff;
    border-top: 18px solid transparent;
    content: "";
    height: 0;
    position: absolute;
    right: 0;
    top: 0;
    transform: rotate(180deg);
    width: 0; }
  &.color--1 {
    background: $primary-color; }
  &.color--2 {
    background: linear-gradient( 8deg, rgb(153,50,245) 0%, rgb(113,51,243) 17%, rgb(73,51,241) 45%, rgb(73,80,248) 71%, rgb(73,108,254) 100%); } }



@for $i from 0 through 100 {
	.f-size-#{$i} {
		font-size: $i + px; } }
@media (max-width: 1199.98px) {
  @for $i from 0 through 100 {
    .f-size-lg-#{$i} {
      font-size: $i + px; } } }
@media (max-width: 991.98px) {
  @for $i from 0 through 100 {
    .f-size-md-#{$i} {
      font-size: $i + px; } } }
@media (max-width: 767.98px) {
  @for $i from 0 through 100 {
    .f-size-xs-#{$i} {
      font-size: $i + px; } } }

@for $i from 0 through 100 {
	.line-height-#{$i} {
		line-height: $i + px; } }





