.spacer-top {
  height: 120px;
  @include breakpoint(xs) {
    height: 70px; } }
.spacer-bottom {
  height: 85px;
  @include breakpoint(xs) {
    height: 40px; } }
.section-title-spacer {
  height: 40px; }
.rt-margin-top {
  margin-top: 120px;
  @include breakpoint(xs) {
    margin-top: 70px; } }
.rt-section-padding {
  padding: 120px 0;
  @include breakpoint(xs) {
    padding: 70px 0; } }
.spacer-banner-search {
  height: 148px; }


