.box-style__1 {
    border: 1px solid rgba(13, 21, 75, 0.15);
    box-shadow: 0px 3px 7px 0px rgba(55, 94, 246, 0.2);
    @include rt-trs(0.24s);
    background-color: #fff;
    padding: {
      left: 10px;
      right: 18px;
      top: 10px;
      bottom: 10px; }
    @include afterparent();
    &:hover {
        border-color: $primary-color; } }

.hotel-inner-content {
    .hotel-thumb {
        @include afterparent();
        .hotel-bg {

            width: 100%;
            height: 195px;
            border: 1px solid rgba(179, 188, 248, 0.8);
            @include afterparent();
            .inner-icon {
                position: absolute;
                left: 0;
                top: 0;
                height: 30px;
                width: 30px;
                background: linear-gradient( 8deg, rgb(153,50,245) 0%, rgb(113,51,243) 17%, rgb(73,51,241) 45%, rgb(73,80,248) 71%, rgb(73,108,254) 100%);
                color: #fff;
                line-height: 30px;
                text-align: center; }
            .inaner-badge {
                position: absolute;
                left: 0;
                top: 10px;
                width: 100%; } } }
    .top {
        margin-bottom: 10px;
        h5 {
            font-size: 16px;
            font-weight: 500;
            text-transform: capitalize;
            span {
                padding-left: 20px;
                i {
                    color: #a19f9f;
                    &.review {
                        color: #ffab53; } } } }
        p {
            font-size: 13px;
            color: #555555;
            margin: 0 0 0; } }
    .middle-text {
        .left {
            span {
              margin-right: 10px;
              &:last-child {
                margin-right: 0px; }
              &:nth-child(2) {
                color: #496cfe; }
              &:nth-child(3) {
                color: #878484;
                font-size: 12px; } } }
        div {
            a {
                font-size: 14px;
                border: 1px dashed  rgba(85, 175, 50, 0.40);
                display: inline-block;
                padding: 6px 60px;
                text-transform: capitalize;
                border-radius: 999px;
                color: rgba(85, 175, 50, 1s);
                margin-top: 15px;
                @include breakpoint(xs) {
                  padding: 6px 20px; } } }
        .right {
            span {
                &:nth-child(1) {
                    font-size: 12px;
                    color: #878484; }
                &:nth-child(2) {
                    font-size: 24px;
                    font-weight: 600;
                    font-family: $heading-font;
                    color: #496cfe; } } } }
    .footer-elements {
        .left {
            position: relative;
            top: 5px;
            span {
                margin-right: 6px;
                &:last-child {
                    margin-right: 0px; } } } } }



// hote slider
.rt-duel-slider-main {
    .single-main {
        height: 467px;
        border-radius: 5px;
        width: 100%;
        .inner-badge {
            position: absolute;
            left: 0;
            top: 20px;
            width: auto;
            display: inline-block;
            padding: 6px 30px;
            border-top-right-radius: 999px;
            border-bottom-right-radius: 999px;
            font-size: 14px;
            &.badge-bg-1 {
                background-image: linear-gradient( 0deg, rgb(255,170,87) 0%, rgb(254,92,118) 100%);
                box-shadow: 0px 13px 20px 0px rgba(13, 21, 75, 0.4);
                color: #fff; } } } }
.rt-duel-slider-thumb {
    padding-left: 80px;
    padding-right: 80px;
    @include breakpoint(lg) {
        padding-left: 40px;
        padding-right: 40px; }
    @include breakpoint(xs) {
        padding-right: 15px;
        padding-left: 15px; }
    .single--thumb {
        height: 118px;
        border-radius: 5px;
        margin-right: 10px;
        border: 1px solid transparent;
        @include prefixer(appearance, none, ("webkit", "moz"));
        outline: none;
        &:last-child {
            margin-right: 0px; }
        &.slick-current {
            border: 1px solid rgba(11, 29, 151, 0.8); } }

    .arrow_left, .arrow_right {
        position: absolute;
        left: 15px;
        top: 50%;
        transform: translateY(-50%);
        text-align: center;
        color: #333;
        font-size: 25px;
        z-index: 66;
        cursor: pointer;
        opacity: 1;
        &:focus {
            outline: none; }
        @include breakpoint(xs) {
          @include hw(35px);
          background-color: $primary-color;
          color: #fff;
          left: 0;
          border-radius: 5px; } }


    .arrow_right {
        left: auto;
        right: 15px; } }

// hotel tab nav style
.rt-tab-nav-1 {
    li {
        a {
            background-color: rgb(255, 255, 255);
            box-shadow: 0px 5px 10px 0px rgba(55, 94, 246, 0.2);
            border: 1px solid rgba(42, 56, 76,  0.15);
            text-align: center;
            border-radius: 5px;
            font-size: 14px;
            color: #4f4f4f;
            transition: all 0.24s ease-in-out;
            display: inline-block;
            padding: 6px 30px;
            text-transform: uppercase;
            @include breakpoint(md) {
                margin: 5px; }

            i {
                font-size: 18px; }
            &.active {
                background-image: linear-gradient( 8deg, rgb(153,50,245) 0%, rgb(73,51,241) 53%, rgb(73,80,248) 70%, rgb(73,108,254) 100%);
                color: #fff; } }


        margin-right: 10px;
        &:last-child {
            margin-right: 0px; } }
    &.pill {
        li a {
            border-radius: 999px; } } }


// tour
.single-tour {
    @include afterparent();
    .icon-thumb {
        border: 1px dotted rgba(73, 108, 254, 0.702);
        background: #fff;
        font-size: 45px;
        height: 65px;
        width: 65px;
        line-height: 53px;
        text-align: center;
        i {
            background: linear-gradient( 8deg, rgb(153,50,245) 0%, rgb(113,51,243) 23%, rgb(73,51,241) 58%, rgb(73,80,248) 81%, rgb(73,108,254) 100%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent; } } }



.tour-carosel-active {
    .owl-item img {
        display: inline-block !important; }

    .owl-nav .owl-prev, .owl-nav .owl-next {
        position: absolute;
        left: 0px;
        bottom: 10px;
        @include hw(65px);
        background-color: transparent !important;
        text-align: center;
        border-radius: 50%;
        color: #333 !important;
        font-size: 30px !important;
        border: 1px solid #333 !important;
        &:focus {
            outline: none; }
        &:hover {
            border-color: rgb(153,50,245) !important;
            i {
                background: linear-gradient( 8deg, rgb(153,50,245) 0%, rgb(113,51,243) 23%, rgb(73,51,241) 58%, rgb(73,80,248) 81%, rgb(73,108,254) 100%);
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent; } } }
    .owl-nav .owl-next {
        left:  80px; } }

.hote-roombgtab {
    height: 131px;
    margin-left: -10px;
    margin-top: -10px;
    margin-right: -18px; }



.service-amitence-box {
    .media-body {
        border-left: 1px solid rgba(0, 0, 0, 0.302)\; } }
.single-commnets {
    .replay-cmnt {
        position: absolute;
        right: 30px;
        top: 0px; } }


// trip-timeline
.trip-timeline {
    padding-left: 190px;
    @include breakpoint(md) {
        padding-left: 0px; }
    @include afterparent();
    &:after {
        position: absolute;
        left: 70px;
        top: 0;
        width: 2px;
        height: 100%;
        background-color: rgba(144, 164, 254, 0.92);
        content: '';
        @include breakpoint(md) {
            display: none; } }
    &:before {
        position: absolute;
        left: 70px;
        top: 0px;
        width: 2px;
        height: 94px;
        background-color: #fff;
        content: '';
        z-index: 2;
        @include breakpoint(md) {
            display: none; } }


    li.time-line-item {
        @include afterparent();
        .timeline-counter {
            height: 164px;
            width: 164px;
            border: 1px solid rgba(144, 164, 254, 0.90);
            border-radius: 50%;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: -200px;
            background-color: #fff;
            @include breakpoint(md) {
                  display: none; }
            .inner-item {
                @extend .bg-gradient-primary;
                color: #ffffff;
                height: 110px;
                width: 110px;
                border-radius: 50%;
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: column;
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%); } }

        &:last-child {
            &:before {
                position: absolute;
                left: -121px;
                bottom: 0px;
                width: 2px;
                height: 93px;
                background-color: #fff;
                content: '';
                z-index: 2; } } } }
