// top header css
.top-header {
  padding: 16px 0 0; }
.top-message {
  margin: 0;
  padding: 0;
  list-style: none;
  li {
    display: inline-block;
    font-size: 14px;
    padding-right: 15px;
    @include rt-trs(0.24s);
    &:last-child {
      padding-right: 0px; }
    color: #4f4f4f;
    i {
      color: #4f4f4f;
      font-size: 16px;
      padding-right: 3px;
      @include rt-trs(0.24s); }
    a {
      display: block;
      color: #4f4f4f;
      @include rt-trs(0.24s);

      i {
        color: #4f4f4f; }
      &:hover {
        color: $primary-color;
        i {
          color: $primary-color; } } } } }
.top-social {
  margin: 0;
  padding: 0;
  list-style: none;
  li {
    display: inline-block;
    padding-right: 30px;
    &:last-child {
      padding-right: 0px; }
    a {
      display: block;
      text-align: center;
      color: #ccc;
      font-size: 14px;
      color: #d9e0fd;
      i {
        padding-right: 5px; }
      @include rt-trs(0.24s); } } }
.top-header {
  &.rt-light-text {
    .top-message li a , .top-message li, .top-message li i {
      color: #fff; }
    .top-message li {
      &:hover {
        color: $primary-color;
        i {
          color: $primary-color; }
        a {
          color: $primary-color; } } } } }
