.singleTbox-1 {
    @include afterparent();
    .testi-thumb {
        height: 260px;
        width: 260px;
        @include afterparent();
        box-sizing: border-box;
        border-radius: 50%;
        background: linear-gradient( 0deg, rgb(157,91,254) 0%, rgb(56,144,254) 100%);
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 35px;
        @include breakpoint(lg) {
          height: 160px;
          width: 160px; }
        .inner-bg {
            position: absolute;
            left: 6px;
            top: 6px;
            background-repeat: no-repeat;
            background-position: center center;
            background-size: cover;
            width: 248px;
            height: 248px;
            border-radius: 50%;
            box-shadow: 0px 17px 20px 0px rgba(25, 80, 161, 0.35);
            @include breakpoint(lg) {
              width: 190px;
              height: 190px;
              left: 5px;
              top: 5px; }
            @include breakpoint(xs) {
              width: 150px;
              height: 150px;
              left: 5px;
              top: 5px; } }
        .social-badge {
            @include hw(60px);
            background: linear-gradient( 35deg, rgb(157,91,254) 0%, rgb(56,144,254) 100%);
            color: #ffffff;
            position: absolute;
            right: 20px;
            top: 0;
            border-radius: 50%;
            @include breakpoint(lg) {
              @include hw(40px); } } }
    .autor-bio {
        margin-bottom: 30px;
        h5 {
            font-size: 22px;
            font-weight: 700;
            color: #333;
            text-transform: capitalize; }

        p {
            font-size: 14px; }
        .rating {
            color: #ffab53;
            font-size: 14px; } }
    .inner-content {
        p {
            font-size: 18px;
            line-height: 28px; } } }





.testimonials-area {
    @include afterparent();
    .rt-shape-emenetns-1 {
      position: absolute;
      right: -200px;
      bottom: 100px;
      width: 522px;
      height: 558px;
      @extend .rtbgprefix-contain;
      @include breakpoint(xl) {
        @include hw(400px); }
      @include breakpoint(md) {
        @include hw(200px);
        right: -100px; }
      @include breakpoint(xs) {
        display: none; } } }

.rt-light-text {
    .singleTbox-1 {
        .autor-bio {
            p {
                color: #fff; }
            h5 {
                color: #fff; } }
        .inner-content {
            p {
                color: #fff; } } } }


@import "round-testi";





