.single-blg-post {
    position: relative;
    margin-bottom: 30px;
    border: 1px solid  rgba(15, 54, 97, 0.2);
    padding: 8px;
    @include rt-trs(0.34s);
    .post-thumbnail {
        display: block;
        @include afterparent();
        .meta-date {
            position: absolute;
            right: 0;
            top: -3px;
            //background-image: linear-gradient( 8deg, rgb(153,50,245) 0%, rgb(73,51,241) 53%, rgb(73,80,248) 70%, rgb(73,108,254) 100%)
            background-image: url(../images/all-img/blog-meta.png);
            @extend .rtbgprefix-contain;
            width: 87px;
            height: 87px;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            color: #fff;
            font-family: $heading-font;
            font-weight: 200;


            span {
                padding-left: 10px;
                &:nth-child(1) {
                    font-size: 30px; }

                &:nth-child(2) {
                    font-size: 16px;
                    margin-top: 5px; } } } }

    .entry-content {
        padding-left: 30px;
        padding-right: 30px;
        padding-bottom: 15px;
        .entry-header {
            .entry-title {
                font-size: 30px;
                color: #000;
                line-height: 40px;
                word-break: break-word;
                font-weight: 600;
                margin: 30px 0 20px;
                transition: all 0.24s ease-in-out;
                @include breakpoint(lg) {
                  font-size: 24px;
                  line-height: 36px; }
                a {
                    color: #000;
                    &:hover {
                        color: $primary-color; } } }
            .entry-meta {
                margin-bottom:  20px;
                span {
                    display: inline-block;
                    margin-right: 27px;
                    @include breakpoint(lg) {
                      margin-right: 20px; }
                    font-size: 14px;
                    a {
                        color: #3d446f;
                        transition: all 0.24s ease-in-out;
                        i {
                            color: #3d446f;
                            padding-right: 5px; }
                        &:hover {
                            color: $primary-color;
                            i {
                                color: $primary-color; } } }
                    &:last-child {
                        margin-right: 0px; } } } }
        p {
            font-size: 15px;
            color: #3d446f;
            line-height: 26px; }
        .entry-footer {
            padding-top: 24px;
            margin-top: 24px;
            border-top: 1px solid  rgba(15, 54, 97, 0.2);
            a {
                font-size: 14px;
                text-transform: uppercase;
                color: #0d154b;
                font-weight: 700;
                @include rt-trs(0.24s);
                &:hover {
                    color: $primary-color; } } } }
    &:hover {
        box-shadow: 0px 5px 20px 0px rgba(46, 25, 167, 0.5);

        border-color: rgba(73, 108, 254, 0.5); } }
