.rt-hidden-search {
    position: fixed;
    left: 50%;
    top: 50%;
    width:  70px;
    height: 70px;
    z-index: 999;
    border-radius: 50%;
    overflow: hidden;
    transform: translate(-50%, -50%);
    background-color: $primary-color;
    transition: all .4s ease-in-out;
    @include hidden();
    .rt-search-close {
        position: absolute;
        right: 30px;
        top: 30px;
        color: #fff;
        font-size: 20px;
        cursor: pointer; }
    &.rt-search-active {
        @include vis();
        width: 100%;
        height: 100%;
        border-radius: 0px;
 } }        //animation: zoomIn 400ms ease-in-out
.search-height {
    height: 100vh; }
