.app-area {
    @include afterparent();
    .rt-shape-elements-1 {
        position: absolute;
        left: -2px;
        top: 0;
        width: 857px;
        height: 752px;
        @include breakpoint(xxl) {
            width: 700px; }
        @include breakpoint(xl) {
            width: 515px; }
        @include breakpoint(lg) {
            width: 430px; }
        @include breakpoint(md) {
            width: 650px; }
        @include breakpoint(xs) {
          width: 100%; } } }
