.about-area {
    @include afterparent();
    .rt-design-elmnts {
        position: absolute;
        left: 50px;
        width: 610px;
        height: 460px;
        top: 50%;
        transform: translateY(-50%);
        @include breakpoint(xxl) {
          width: 450px; }
        @include breakpoint(xl) {
          width: 280px;
          height: 280px; }
        @include breakpoint(lg) {
          display: none; } } }

