    opacity: 0;
    visibility: hidden;
    transition: all 0.36s $ease-in-sine;
    color: #fff;
    border-radius: 10px; }
  &:hover {
    color: #fff;
    &:after {
      opacity: 1;
      visibility: visible; } }
  &.pill {
    &:hover {
      &:after {
        border-radius: 999px; } } }
  &.rt-rounded {
    &:hover {
      &:after {
        border-radius: 10px; } } } }



.rt-outline-gradientL {
  background: transparent;
  color: #fff;
  border: 1px solid #fff;
  @include afterparent();
  &:after {
    @include after(absolute, 0,0, 100%, 100%);
    z-index: -1;
    background: linear-gradient( -35deg, rgb(153,50,245) 0%, rgb(73,51,241) 53%, rgb(73,80,248) 70%, rgb(73,108,254) 100%);
    opacity: 0;
    visibility: hidden;
    transition: all 0.24s $ease-in-sine;
    color: #fff; }
  &:hover {
    color: #fff;
    border: 1px solid transparent;
    &:after {
      opacity: 1;
      visibility: visible;
      box-shadow: 0px 15px 30px 0px rgba(13, 21, 75, 0.2); } } }

.rt-outline-gradientP {
  background: transparent;
  color: #333;
  border-radius: 3px;
  border: 1px solid $primary-color;
  @include afterparent();
  &:after {
    @include after(absolute, 0,0, 100%, 100%);
    z-index: -1;
    background: linear-gradient(to right, $primary-color, $secondary-color);
    opacity: 0;
    visibility: hidden;
    transition: all 0.24s $ease-in-sine;
    color: #333; }
  &:hover {
    color: #fff;
    border: 1px solid transparent;
    &:after {
      opacity: 1;
      visibility: visible; } } }
.rt-outline-gradientS {
  background: transparent;
  color: #333;
  border-radius: 3px;
  border: 1px solid $secondary-color;
  @include afterparent();
  &:after {
    @include after(absolute, 0,0, 100%, 100%);
    z-index: -1;
    background: linear-gradient(to right, $secondary-color, $primary-color);
    opacity: 0;
    visibility: hidden;
    transition: all 0.24s $ease-in-sine;
    color: #333; }
  &:hover {
    color: #fff;
    border: 1px solid transparent;
    &:after {
      opacity: 1;
      visibility: visible; } } }



.rt-outline-primary:not(:disabled):not(.disabled).active, .rt-outline-primary:not(:disabled):not(.disabled):active, .show>.rt-outline-primary.dropdown-toggle {
  background: $primary-color;
  border-color: $primary-color;
  box-shadow: none; }

.rt-outline-primary:not(:disabled):not(.disabled).active:focus, .rt-outline-primary:not(:disabled):not(.disabled):active:focus, .show>.rt-outline-primary.dropdown-toggle:focus {
  box-shadow: none; }



.aps-button-gruop {
  .rt {
    margin: 0 5px;
    &:first-child {
      margin-left: -5px; } } }

// rt linbk
.rt-lnk {
  text-decoration: underline;
  color: $body-font-colr; }
.vp-pagination__style-default .vp-pagination__item>* {
  @extend .rt;
  @extend .rt-secondary;
  border-radius: 999px; }
.vp-filter__style-default .vp-filter__item.vp-filter__item-active>* {
  @extend .rt-secondary;
  border-radius: 3px; }



