
.deal-area {
    padding: 100px 0 190px;
    @include breakpoint(md) {
      padding: 0;
      .rt-section-title-wrapper {
          background: $gradient-primary;
          padding: 30px;
          border-bottom-left-radius: 15px;
          border-bottom-right-radius: 15px; } } }

.deal-carosel-active {
    @include breakpoint(md) {
      padding-left: 30px;
      padding-right: 30px; }

    .owl-nav .owl-prev, .owl-nav .owl-next {
        position: absolute;
        left: 31%;

        bottom: -60px;
        transform: translateY(-50%);
        @include hw(65px);
        background-color: #fff !important;
        text-align: center;
        border-radius: 50%;
        color: #333 !important;
        box-shadow: 0px 4px 10px 0px rgba(55, 94, 246, 0.4);
        font-size: 30px !important;
        &:focus {
            outline: none; }
        @include breakpoint(xxl) {
          left: 25%; }
        @include breakpoint(xl) {
          left: 22%; }
        @include breakpoint(md) {
          bottom: -80px; }
        @include breakpoint(xs) {
          left: 15%;
          @include hw(40px);
          font-size: 16px !important;
          bottom: -30px; } }
    .owl-nav .owl-next {
        left: 35%;
        @include breakpoint(xxl) {
          left: 30%; } } }




.single-deal-carosel {
    .deal-bg {
        height: 642px;
        @extend .rtbgprefix-cover;
        border-top-right-radius: 15px;
        border-bottom-right-radius: 15px;
        background-color: #f4eafe;
        @include breakpoint(md) {
          border-top-left-radius: 15px;
          border-top-right-radius: 15px;
          border-bottom-right-radius: 0px; }
        .inner-content {
            position: absolute;
            left: 0;
            bottom: 50px;
            text-align: center;
            width: 100%;
            @include breakpoint(lg) {
              padding: {
                left: 15px;
                right: 15px; } }
            h4 {
                font-size: 24px;
                text-transform: capitalize;
                font-weight: 600;
                color: #fff;
                @include breakpoint(xl) {
                    font-size: 20px; }
                @include breakpoint(lg) {
                  font-size: 18px; } }
            p {
                font-size: 14px;
                color: #fff;
                margin: 0 0 0; } } }
    .rt-section-title-wrapper {
        padding-right: 450px;
        @include breakpoint(xxl) {
          padding-right: 300px; }
        @include breakpoint(xl) {
          padding-right: 100px; }
        @include breakpoint(lg) {
          padding-right: 30px; }

        .rt-section-title {
            margin: 0 0 25px; } }
    .deal-bottom-content {
        .rating-box {
            border: 1px dashed #fff;
            padding: 20px;
            max-width: 252px;
            min-height: 117px;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            text-align: center;
            span {
                font-size: 14px;
                &:nth-child(1) {
                    font-size: 16px;
                    text-transform: uppercase;
                    position: relative;
                    &:after {
                        position: absolute;
                        left: -20px;
                        top: -5px;
                        content: '\efcd';
                        font-family: 'icofont';
                        font-size: 12px; }

                    &:before {
                        position: absolute;
                        right: -20px;
                        top: -5px;
                        content: '\efce';
                        font-family: 'icofont';
                        font-size: 12px; } }

                &:nth-child(3) {
                    font-size: 12px; } }


            span:nth-child(2) {
                i {
                    color: #ffab53; } } }
        h4 {
            font-size: 24px;
            font-weight: 700;
            margin: 0 0 27px; }

        p {
            font-size: 14px;
            line-height: 26px;
            margin-bottom: 30px; } } }



