.rt-single-icon-box {
    display: flex;
    @include afterparent();
    padding: {
        left: 30px;
        top: 25px;
        bottom: 24px;
        right: 30px; }
    border: 1px solid  rgba(50, 50, 50, 0.5);
    background-color: rgb(255, 255, 255);
    border-radius: 10px;
    margin-bottom: 30px;
    align-items: start;
    @include rt-trs(0.24s);
    .icon-thumb {
        @include flex-width(65px);
        height: 58px;
        margin-right: 30px;
        img {
            @include rt-trs(0.24s); }
        i {
            font-size: 30px;
            color: $primary-color; } }
    .iconbox-content {
        h5 {
            font-size: 24px;
            font-weight: 600;
            margin-bottom: 13px; }
        p {
            margin: 0 0 0; } }
    &.icon-center {
        display: block;
        padding: {
            left: 25px;
            right: 25px;
            top: 22px;
            bottom: 22px; }
        border-color: rgba(0, 0, 0, 0.2);
        .icon-thumb {
            @include flex-width(100%);
            height: auto;
            margin-right: 0px;
            margin-bottom: 20px; }
        .iconbox-content {
            p {
                font-size: 14px; }
            h5 {
                margin: 0 0 20px; }
            .read-morebtn {
                margin-top: 18px;
                a {
                    font-size: 14px;
                    text-transform: uppercase;
                    color: #496cfe;
                    font-weight: 600;
                    i {
                        font-size: 20px; } } } } }
    &.shdoaw-style {
        border-radius: 20px;
        box-shadow: 0px 3px 10px 0px rgba(13, 21, 75, 0.2),0px 15px 10px 0px rgba(13, 21, 75, 0.15);
        background-color: #fff;
        border-color: transparent; }



    &:hover {
        border-color: rgb(73, 108, 254);
        box-shadow: 0px 13px 40px 0px rgba(73, 93, 251, 0.3);
        .icon-thumb {
            img {
                animation: rotate-in-2-fwd .6s cubic-bezier(.25,.46,.45,.94) both; } }
        &.icon-center {
            box-shadow: none; } } }



